import './ModalWindow.css'

import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import axios from 'axios';

import Cross from '../../images/Cross.png'
import Tick from '../../images/Tick.png'
import Card from '../../images/Card.png'
import Arrow from '../../images/Arrow.png'

export default function ModalBooks({ book }) {
  const [books, setBooks] = useState([]);

  const [modalIsOpen, setModalIsOpen] = useState(false);
  let agree = '';
  let agreeSrc = {src: ''};;

  useEffect(() => {
    fetch('http://localhost:8080/books')
      .then(res => res.json())
      .then(data => setBooks(data))
      .catch(error => console.log(error));
  }, []);

  if (book.available > 0) {
    agree = '  '
    agreeSrc.src = Tick
  } else {
    agree = '  '
    agreeSrc.src = Cross
  }

  function addCart() {
    const token = localStorage.getItem('isAuth');
      axios.post('http://localhost:8080/cart', { bookId: book._id, bookImg: book.img, bookTitle: book.title,
      bookAuthor: book.author, bookAvailable: book.available, bookCost: book.cost  }, 
      { headers: { Authorization: `Bearer ${token}` } })
      .then(response => { console.log('   g '); })
      .catch(error => { console.error(error); });
  }

  return (
    <div className='ModalBooks'>
      <button className='buy' onClick={() => setModalIsOpen(true)}>{book.cost}</button>
      <Modal isOpen={modalIsOpen} onRequestClose={() => setModalIsOpen(false)} className='ModalWindow'>
        <button onClick={() => setModalIsOpen(false)} className='close'><img src={Cross} /></button>
        <nav><b>{book.title}</b></nav>
        <span><img src={agreeSrc.src} />{agree}</span>
        <div className='book-info'>
          <div className='left-info'>
            <img src={require(`../../images/books/${book.img}.png`)} className='info-img' />
            <nav></nav>
            <span></span>
            <span>ISBN</span>
            <span></span>
            <span></span>
            <span> </span>
            <span></span>
            <span> </span>
            <span></span>
            <span> </span>
            <span>, </span>
            <span>, </span>
            <span></span>
          </div>

          <div className='right-info'>
            <nav>{book.cost}</nav>
            <button className='addCart' key={books._id} onClick={() => addCart(book._id)}>  </button>
            <span className='safeBuy'><img src={Card}/> </span>
            <span className='comeBack'><img src={Arrow}/>   14 </span>
            <span>{book.author}</span>
            <span>{book.ISBN}</span>
            <span>{book.janr}</span>
            <span>{book.company}</span>
            <span>{book.series}</span>
            <span>{book.age}</span>
            <span>{book.year}</span>
            <span>{book.binding}</span>
            <span>{book.pages}</span>
            <span>{book.weight}</span>
            <span>{book.size}</span>
            <span>{book.language}</span>
          </div>
        </div>

        <nav></nav>
        <span>{book.annotation}</span>
      </Modal>
    </div>
  );
}
